// Bootstrap
import './sass/bootstrap.scss'

// Libs
import Vue from 'vue'
import Router from 'vue-router'

// use
Vue.use(Router)

// router
const router = new Router({
  history: true,
  saveScrollPosition: false,
  transitionOnLoad: true
})

// routerRedirect
router.redirect({
  '*': '/'
})

module.exports = {
  Vue,
  router
}


