export default {
  code: `/* Some example CSS */

  @import url("something.css");

  body {
    margin: 0;
    padding: 3em 6em;
    font-family: tahoma, arial, sans-serif;
    color: #000;
  }

  #navigation a {
    font-weight: bold;
    text-decoration: none !important;
  }

  h1 {
    font-size: 2.5em;
  }

  h2 {
    font-size: 1.7em;
  }

  h1:before, h2:before {
    content: "::";
  }

  code {
    font-family: courier, monospace;
    font-size: 80%;
    color: #418A8A;
  }`,
  option: {
    mode: 'text/css',
    lineNumbers: true,
    lineWrapping: false,
    // theme: 'base16-dark',
    extraKeys: { "Ctrl-Space": "autocomplete" },
  }
}


