export default {
  code: 
  
`<html style="color: green">
  <!-- this is a comment -->
  <head>
    <title>HTML Example</title>
  </head>
  <body>
    The indentation tries to be <em>somewhat &amp;quot;do what
    I mean&amp;quot;</em>... but might not match your style.
  </body>
</html>
`,
  option: {
    lineNumbers: true,
    mode: 'text/html',
    lineWrapping: false,
    theme: 'ambiance'
  }
}


