/*
*
* vue-codemirror
*
*/

// App
import App from './App.vue'

// component
import Index from './components/Index.vue'

// Project
import CodeMirror from 'vue-codemirror'

const projectApp = (Vue, router) => {

  // use
  Vue.use(CodeMirror)

  // routerMap
  router.map({
    '/': {
      name: 'index',
      component: Index
    }
  })

  // Start
  router.start(App, 'app')
}

module.exports = projectApp



