<template>
  <div class="index">

    <!-- banner -->
    <div class="jumbotron">
      <div class="container">
        <div class="text-center">
          <h1>Vue-codemirror</h1>
          <h3>Codemirror component for Vue.js</h3>
          <br>
          <p>
            <a href="https://github.com/surmon-china/vue-codemirror" class="btn btn-primary btn-lg" target="_blank">View on GitHub</a>
            <span>&nbsp;</span>
            <a href="http://codemirror.net/doc/manual.html#config" class="btn btn-warning btn-lg" target="_blank">MORE CONFIG</a>
            <span>&nbsp;</span>
            <a href="http://codemirror.net/demo/theme.html" class="btn btn-success btn-lg" target="_blank">MORE THEMES</a>
            <span>&nbsp;</span>
            <a href="http://codemirror.net/mode/" class="btn btn-info btn-lg" target="_blank">MORE LANGUAGE MODE</a>
          </p>
        </div>
      </div>
    </div>

    <!-- editor lists -->
    <div class="container">

      <!-- data bind -->
      <div class="row">
        <div class="col-xs-12">
          <div class="panel panel-default">
            <div class="panel-heading">
              <h4 class="text-center">
                <span>mode: {{ exampleEditor.option.mode }}</span>
                <span>&nbsp;&nbsp;&nbsp;</span>
                <span>theme: {{ exampleEditor.option.theme }}</span>
              </h4>
            </div>
            <div class="panel-body">
              <div class="col-xs-6">
                <codemirror :code.sync="exampleEditor.code" :options="exampleEditor.option"></codemirror>
              </div>
              <div class="col-xs-6">
                <pre>{{ exampleEditor.code }}</pre>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- lists -->
      <div class="row">
        <div class="col-xs-4" v-for="editor in editors">
          <div class="panel panel-default">
            <div class="panel-heading">
              <h4 class="text-center">
                <span v-if="typeof editor.option.mode == 'string'">{{ editor.option.mode }}</span>
                <span v-if="typeof editor.option.mode == 'object'">{{ editor.option.mode.name }}</span>
              </h4>
            </div>
            <div class="panel-body">
              <codemirror :code.sync="editor.code" :options="editor.option"></codemirror>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  import editors from './editors'
  const editorsKeys = Object.keys(editors)
  const editorsDemos = []
  editorsKeys.forEach(key => { editorsDemos.push(editors[key]) })
  // console.log(editorsDemos)

  export default {
    data () {
      const exampleEditor = {
        code: `export default {
          data () {
            return {
              exampleCode: 'const a = 10',
              editorOption: {
                tabSize: 4,
                styleActiveLine: true,
                lineNumbers: true,
                line: true,
                // mode string config
                mode: 'text/javascript',
                // mode object config
                /*
                 mode: { 
                  name: "python",
                  version: 3,
                  singleLineStringErrors: false
                 },
                 */
                lineWrapping: true,
                theme: 'base16-dark'
              }
            }
          }
        }`,
        option: {
          tabSize: 4,
          styleActiveLine: true,
          lineNumbers: true,
          line: true,
          mode: 'text/javascript',
          lineWrapping: true,
          theme: 'base16-dark'
        }
      }
      return {
        exampleEditor: exampleEditor,
        editors: editorsDemos
      }
    }
  }
</script>

<style lang="scss">
  pre {
    max-height: 300px;
  }
</style>


