export default {
  code: `<?php
  $a = array('a' => 1, 'b' => 2, 3 => 'c');

  echo "$a[a] \${a[3] /* } comment */} {$a[b]} \$a[a]";

  function hello($who) {
    return "Hello $who!";
  }
  ?>
  <p>The program says <?= hello("World") ?>.</p>`,
  option: {
    mode: 'application/x-httpd-php',
    lineNumbers: true,
    lineWrapping: false,
    theme: 'base16-light',
  }
}


